<style>
  .photo {
    padding: 10px 10px 0 0;
  }
  .photo img {
    height: 150px;
    width: 100%;
    object-fit: cover;
  }
  h3.title {
    margin-top: 30px;
  }
</style>

<template>
  <v-ons-page>
    <navbar title="Camera Test" enable-back></navbar>
    <div class="main-content">
      <v-ons-button modifier="cta" @click="takeFromCamera">
        <v-ons-icon icon="fa-camera"></v-ons-icon> Take photo
      </v-ons-button>
      <v-ons-button modifier="cta" @click="takeFromGallery">
        <v-ons-icon icon="fa-photo"></v-ons-icon> Open gallery
      </v-ons-button>

      <h3 class="title">Selected Photos</h3>
      <v-ons-row>
        <v-ons-col class="photo" width="50%" v-for="(item, key) in photos" :key="key">
          <img :src="item.src" />
        </v-ons-col>
      </v-ons-row>
    </div>
  </v-ons-page>
</template>

<script>
export default {
  data() {
    return {
      isLoading: false,
      photos: [
        {
          src: 'data:image/png;base64,',
        },
        {
          src: 'data:image/png;base64,',
        },
      ],
    };
  },
  methods: {
    takeFromCamera() {
      /* eslint-disable no-undef */
      this.devices = [];  // force reset devices list
      if (typeof navigator.camera === 'undefined') {
        this.$ons.notification.alert('Can\'t take picture on mobile web');
      } else {
        const cameraOptions = {
          destinationType: 0,
          sourceType: 1,
        };
        navigator.camera.getPicture((photoSrc) => {
          console.log('photo captured!');
          this.photos.push({ src: `data:image/jpeg;base64,${photoSrc}` });
        }, (error) => {
          console.log('take photo failed!');
          console.log(error);
        }, cameraOptions);
      }
    },
    takeFromGallery() {
      /* eslint-disable no-undef */
      this.devices = [];  // force reset devices list
      if (typeof navigator.camera === 'undefined') {
        this.$ons.notification.alert('Can\'t open gallery on mobile web');
      } else {
        const cameraOptions = {
          destinationType: 0,
          sourceType: 2,
        };
        navigator.camera.getPicture((photoSrc) => {
          console.log('photo retrieved!');
          this.photos.push({ src: `data:image/jpeg;base64,${photoSrc}` });
        }, (error) => {
          console.log('retrieved photo failed!');
          console.log(error);
        }, cameraOptions);
      }
    },
  },
};
</script>



